import { Component, h, Element, State, Host } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-recycle',
  styleUrl: 'cart-recycle.css'
})
export class CartRecycle {
    @Element() root: HTMLElement;
    @State() enabled: boolean;

    componentWillLoad() {
        const update = () => {
            this.enabled = cart.get("recycle").length > 0;
        }

        cart.onChange("recycle", update);
        update();
    }

    send(event: Event) {
        var recycle_headers = new Headers();
        recycle_headers.append('pragma', 'no-cache');
        recycle_headers.append('cache-control', 'no-cache');

        const recycle_form = this.root.querySelector("form");

        event.preventDefault();
        const body = new FormData(recycle_form);

        fetch(cart.get('api').recycleChange, {
            method: 'POST',
            headers: recycle_headers,
            credentials: "same-origin",
            body: body
        });
    }

    render() {
        return <Host class={!this.enabled ? "hidden" : ""}>
            <slot/>

            <form method="post" name="recycle" onChange={e=>this.send(e)}>
                {cart.get("recycle").map(product => 
                    <ks-input-check
                        name={`recycle[${product.id}]`}
                        value={product.name}
                        label={product.name}
                        checked={product.checked}
                    />
                )}
            </form>
        </Host>;
    }
}