import { Component, h, Listen, Element, Prop, State, Method, Event, EventEmitter } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-select-shipping',
  styleUrl: 'cartSelect.css'
})
export class CartSelectShipping {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) name: string = "";
    @Prop({reflect: true, mutable: true}) valid: boolean = false;
    @Prop({reflect: true, mutable: true}) error: boolean = false;

    @State() active: number = -1;
    @State() toggled = false;
    @State() loading = false;

    @Method() async StartLoading() {
        this.loading = true;
    }

    @Method() async ResetLoading() {
        this.loading = false;
    }

    @Method() async Validate() {
        if(!this.valid) {
            this.error = true;
            return false;
        }

        this.error = false;
        return true;
    }

    @Listen('click', { target: 'document' }) OutsideClickHandler(event) {
        const target = event.target as HTMLElement;

        if(this.toggled && !target.closest(`.ks-cart-select[name="${this.name}"]`))
            this.toggled = false;
    }

    componentDidLoad() {
        this.root.classList.add("ks-cart-select");
    }

    @Event() shippingChange: EventEmitter<number>;
    ActivateItem(id: number) {
        if(id != cart.get("activeShipping"))
            this.shippingChange.emit(id);

        this.toggled = false;
        this.active = id;
        this.valid = true;
        this.error = false;
    }

    render() {
        if(this.valid)
            this.active = cart.get("activeShipping");

        if(this.loading) return (
            <div class="center"> 
                <nav uk-spinner></nav>
            </div>
        );

        const activeItem = cart.get("shipping").find((value)=>{
            return value.id == this.active;
        })

        const strings = cart.get("strings");

        const shippingSelect = cart.get("shipping").length > 0 ?
            strings.shippingSelect : strings.shippingMethodMissing;
    
        return[
            <div class="select animation" onClick={()=>{ if(!this.loading) this.toggled = !this.toggled }}>
                <div class="selectSlot">
                    {this.active == -1 ?
                        <ks-cart-select-item name={shippingSelect}></ks-cart-select-item> :
                        <ks-cart-select-item
                            logo={activeItem.logo}
                            name={activeItem.name}
                            price={activeItem.price}>
                        </ks-cart-select-item>
                    }
                    
                </div>
                <div class="selectIcon">
                    <span uk-icon="icon: triangle-down; ratio: 1.3"></span>
                </div>
            </div>,
    
            <div class="items" hidden={!this.toggled}>
                {cart.get("shipping").map((item) =>
                    <ks-cart-select-item
                        key={item.id}
                        logo={item.logo}
                        name={item.name}
                        price={item.price}
                        color={item.color}
                        onClick={()=>this.ActivateItem(item.id)}>
                    </ks-cart-select-item>
                )}
            </div>
        ];
    }
}
