import { Component, h, Element, Listen, State } from '@stencil/core';
import { cart, CartShippingProgress } from '../../../global/data/cart';
import priceFormat from '../../../global/functions/price';

@Component({
  tag: 'ks-cart-progress-bar',
  styleUrl: 'cartProgressBar.css'
})
export class CartProgressBar {
    @Element() root: HTMLElement;

    shippingProgress: CartShippingProgress;

    componentWillLoad() {
        this.shippingProgress = cart.get("shippingProgress");
        this.resizeHandler();

        if(!this.shippingProgress) {
            this.root.hidden = true;
        }
        else {
            this.root.hidden = false;
            this.root.style.backgroundColor = this.shippingProgress.color;
        }
    }

    @State() numberPlacement: boolean = false;
    @Listen('resize', { target: 'window' })
    resizeHandler() {
        if(this.shippingProgress) {
            const barWidth: number = Math.min(cart.get("productValue") / this.shippingProgress.threshold * 100, 100);
            this.numberPlacement = (barWidth / 100 * window.innerWidth) > 200;
        }
    }

    componentWillUpdate() {
        this.componentWillLoad();
    }

    render() {
        const productValue = cart.get("productValue");

        const data = this.shippingProgress;
        if(!data) return null;

        const threshold = priceFormat(data.threshold);
        const current = priceFormat(productValue);

        const barWidth: number = Math.min(productValue / data.threshold * 100, 100);
        const thresholdAchieved: boolean = productValue >= data.threshold;

        const strings = cart.get('strings');

        return[
            <div class="heading ks-text-decorated">
                <div>{data.heading}</div>
                <div>{strings.thresholdPrefix} {threshold}</div>
            </div>,

            <div class="progress" style={{backgroundColor: thresholdAchieved ? "transparent" : ""}}>
                <div class="progress-fill" style={{width: `${barWidth}%`}}>
                    {this.numberPlacement ? 
                        <div class="progress-number">
                            {thresholdAchieved ? <span uk-icon="icon: check; ratio: 1.5"></span> : current}
                        </div>
                    :null}
                </div>

                {!this.numberPlacement ?
                    <div class="progress-number outside">
                        {thresholdAchieved ? <span uk-icon="icon: check; ratio: 1.5"></span> : current}
                    </div>
                :null}
            </div>

        ];
    }
}