import { Component, h, Element } from '@stencil/core';
import { cart } from '../../global/data/cart';

@Component({
  tag: 'ks-cart-summary-container'
})
export class CartSummaryContainer {
    @Element() root: HTMLElement;

    render() {
        const strings = cart.get("strings");

        return[
            <ks-cart-summary text={strings.summaryProductValue} price={cart.get("productValue")}></ks-cart-summary>,
            
            cart.get("otherValues").map(item => 
                <ks-cart-summary text={item.name} price={item.value} emphasis={item.value < 0}></ks-cart-summary>
            ),

            <ks-cart-summary large text={strings.summaryTotalValue} price={cart.get("totalValue")}></ks-cart-summary>
        ];
    }
}