import { Component, h, Prop, Element, Event, EventEmitter } from '@stencil/core';
import { cart } from '../../../global/data/cart'

@Component({
  tag: 'ks-cart-country-select',
  styleUrl: 'cartCountrySelect.css'
})
export class CartCountrySelect {
    @Element() root: HTMLElement;
    @Prop() heading: string;

    @Event() countryChange: EventEmitter<string>;
    ChangeHandler(event: Event) {
        const target = event.target as HTMLSelectElement;
        this.countryChange.emit(target.value);
    }

    render() {
        return[
            <ks-cart-heading>{this.heading}</ks-cart-heading>,
            <div class="select">
                <div class="icons">
                    <span uk-icon="icon: world; ratio: 1.2;"></span>
                    <span uk-icon="icon: triangle-down; ratio: 1.2;"></span>
                </div>
                <select onChange={(ev)=>this.ChangeHandler(ev)}>
                    {cart.get('countries').map( country => 
                        <option value={country.value} selected={country?.selected}>{country.name}</option> 
                    )}
                </select>
            </div>
        ];
    }

}