import { Component, h } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-product-message',
  styleUrl: 'cartProductMessage.css'
})
export class CartProductMessage {

    render() {
        const message = cart.get("productMessage");
        if(!message) return null;

        return <a href={message.link}>{message.message}</a>;
    }
}