ks-cart-product-message {
    display: flex;
    justify-content: center;
    align-items: center;
}

ks-cart-product-message a {
    display: block;
    margin: -30px 10px 15px 10px;
    max-width: 600px;
    padding: 10px 30px;
    border-radius: 50px;
    box-sizing: border-box;
    
    text-align: center;
    text-decoration: none !important;
    
    color: white !important;
    background-color: black;
    background-size: 300% 300%;
    background-image: linear-gradient(
            -45deg, 
            #e01515 0%,
            #da1fb1 100%
    );
    animation: product-message-bg-animation 4s ease infinite;

    opacity: 1.0;
    transition: opacity 0.3s ease;
}

ks-cart-product-message a:hover {
    opacity: 0.8;
}

ks-cart-product-message a:active {
    opacity: 0.6;
}

@keyframes product-message-bg-animation { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

@media screen and (max-width: 959px) {
    ks-cart-product-message a {
        margin: 0 10px 20px 10px;
    }
}