import { Component, h, Prop, State, Element } from '@stencil/core';
import { cart } from '../../global/data/cart';

@Component({
  tag: 'ks-cart-buttons',
  styleUrl: 'cartButtons.css'
})
export class CartButtons {
  @Element() root: HTMLElement;

  @Prop() href: string = "";

  @State() loadingDelayed: boolean = false;
  loadingTimeout: NodeJS.Timeout;

  LoadingWatcher(loading) {
    if(loading) {
      this.loadingTimeout = setTimeout(() => {
        this.loadingDelayed = true;
      }, 500);
    }

    else {
      clearTimeout(this.loadingTimeout);
      this.loadingDelayed = false;
    }
  }


  componentDidLoad() { 
    this.LoadingWatcher(cart.get("loading"));
    cart.onChange("loading", (loading)=>this.LoadingWatcher(loading))
  }

  async clickHandler() {
    if(!cart.get("loading")) {
      const shippingSelect = document.querySelector("ks-cart-select-shipping") as HTMLKsCartSelectShippingElement;
      const paymentSelect = document.querySelector("ks-cart-select-payment") as HTMLKsCartSelectPaymentElement;

      const shippingValid = await shippingSelect.Validate();
      const paymentValid = await paymentSelect.Validate();

      if(shippingValid && paymentValid)
        window.location.href = this.href;

      else {
        const inputTop = shippingSelect.getBoundingClientRect().top - document.body.getBoundingClientRect().top;

        if('scrollBehavior' in document.documentElement.style)
          window.scroll({top: inputTop -100, behavior: "smooth"});
        else window.scroll(0, inputTop -100);
      }
    }
  }

  render() {
    return(
        <button class="confirm uk-button uk-button-danger ks-text-decorated" onClick={()=>this.clickHandler()}>
            {this.loadingDelayed && cart.get("loading") ? 
                <div class="uk-animation-fade" uk-spinner></div> :
                <span>{cart.get("strings").submitButton}</span>
            }
        </button>
    );
  }
}