import { Component, h, Element } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-product-container'
})
export class CartProductContainer {
    @Element() root: HTMLElement;

    render() {
        const products = Object.entries(cart.get("products"));
        const string = cart.get('strings');

        return[
            <ks-cart-product-heading 
                removable
                productTableNames={string.productTableNames}
                productTablePrices={string.productTablePrices}
                productTableCount={string.productTableCount}
                productTableRemove={string.productTableRemove}
            ></ks-cart-product-heading>,
            
            products.map(([id, product]) => 
                <ks-cart-product removable key={id}
                    product-id={id}
                    name={product.name}
                    link={product.link}
                    img={product.image}
                    price={product.currentPrice}
                    amount={product.quantity}
                    max-amount={product.maxQuantity}
                    shipping-time={product.shippingTime}
                    count-unit={string.countUnit}
                ></ks-cart-product>
            ),

            <ks-cart-product-price 
                amount={cart.get("productAmount")}
                price={cart.get("productValue")}
                loading={cart.get("loadingProducts")}
                shipping-time={cart.get("totalShippingTime")}
                productTableTotal={string.productTableTotal}
            ></ks-cart-product-price>
        ];
    }
}