ks-cart-country-select {
    display: block;
}

ks-cart-country-select .select {
    position: relative;
    height: 35px;
    margin-bottom: 35px;
}

ks-cart-country-select .select .icons {
    position: absolute;
    width: 50px;
    height: 50px;
    padding: 13px;

    top: 0;
    right: 0;

    pointer-events: none;

    opacity: 1.0;
    transition: opacity 0.3s ease;

    box-sizing: border-box;
        

    background-color: #252525;
    color: white;
}

ks-cart-country-select .select .icons span { position: absolute;}
ks-cart-country-select .select span:last-of-type { opacity: 0.0; }

ks-cart-country-select .select select {
    height: 50px;
    width: 100%;

    padding: 0 20px;

    outline: none;
    border: none;

    -moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;

    background-color: #f8f8f8;
    border: 1px solid #d2d2d2;

    text-align-last: left;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;

    color: #151515;
    transition: color 0.3s ease;
}

ks-cart-country-select .select option { font-size: 16px; }
ks-cart-country-select .select:hover select { color: #606060; }

ks-cart-country-select .select span:first-of-type { opacity: 0.0 !important; }
ks-cart-country-select .select span:last-of-type { opacity: 1.0 !important; }

ks-cart-country-select .select:hover span:first-of-type { opacity: 0.0; }
ks-cart-country-select .select:hover span:last-of-type { opacity: 1.0; }