import { Component, h, Element, State } from '@stencil/core';

import { cart, easyprotectInsured, CartProduct } from '../../../global/data/cart';
import { easyprotectChange } from '../../../global/functions/cart';

@Component({
  tag: 'ks-cart-easyprotect-dialog',
  styleUrl: 'cart-easyprotect-dialog.css'
})
export class CartEasyprotectDialog {
    @Element() root: HTMLElement;

    @State() eligible: CartProduct[];
    @State() active: string[] = [];
    @State() step: number = 0;

    componentWillLoad() {
        const update = () => {
            this.updateEligible();
        }

        update();
        cart.onChange("easyprotect", update);
        cart.onChange("insured", update);
    }

    overlay: HTMLKsOverlayElement
    componentDidLoad() {
        this.overlay = this.root.querySelector("ks-overlay") as HTMLKsOverlayElement;
    }

    updateEligible() {
        const insured = Object.keys(cart.get("insured"));
        const available = Object.keys(cart.get("easyprotect"));

        this.eligible = available
            .filter(id => !insured.includes(id) && cart.get('products')[id] !== undefined)
            .map(id => cart.get('products')[id]);
    }

    updateStep() {
        if(this.eligible.length == 1) {
            this.active = [this.eligible[0].id];
            this.step = 2;
        }
    }

    render() {
        const strings = cart.get('strings');

        const enabled = this.eligible.length > 0

        const products = this.eligible.length > 1 && this.step < 1 ? "" : "hidden";
        const productsHide = this.step == 1 ? "hide" : "";
        const warranty = this.eligible.length == 1 || this.step >= 2 ? "" : "hidden";
        const warrantyHide = this.step == 1 ? "hide" : "";

        return [
            <ks-button tall name={strings.addWarranty} onClick={()=>this.show()}
                class={enabled ? null : "hidden"}
            />,
            <ks-overlay>
                <div class={`content ${products} ${productsHide}`}>
                    {this.products()}
                </div>
                <div class={`content ${warranty} ${warrantyHide}`}>
                    {this.warranty()}
                </div>
            </ks-overlay>
        ];
    }

    products() {
        return <div class="products">
            <slot name="products"/>
            <div class="items">
                {this.eligible.map(item => 
                    <ks-cart-easyprotect-product image={item.image} name={item.name}
                        warranty={item.warranty}
                        onClick={()=>this.toggle(item.id)}
                        active={this.active.includes(item.id)}
                    />
                )}
            </div>
            <ks-button tall name="Przejdź dalej"
                onClick={()=> this.addProducts()}
                disabled={this.active.length <= 0}
            />
            <div class="close" onClick={()=>this.hide()}>
                <ks-icon name="x" size={1.2}/>
            </div>
        </div>;
    }

    warranty() {
        const strings = cart.get('strings');

        const chosen = Object.keys(cart.get("easyprotect"))
            .filter(id => this.active.includes(id));

        return [
            <div class="warranty">
                <slot name="warranty"/>
                <div class="items">
                    {chosen.map(id => <ks-cart-easyprotect-warranty product-id={id}/> )}
                </div>
                <ks-button tall name={strings.addWarranty}
                    onClick={()=>this.addWarranty()}
                    disabled={this.active.length <= 0}
                />
                <div class="close" onClick={()=>this.hide()}>
                    <ks-icon name="x" size={1.2}/>
                </div>
                {this.eligible.length > 1 ?
                    <div class="back" onClick={()=>this.back()}>
                        <ks-icon name="chevron-left" size={1.2}/>
                    </div>
                : null}
            </div>,
            this.step == 3 ?
                <div class="loading">
                    <ks-loader large dark/>
                </div>
            : null
        ];
    }

    toggle(id: string) {
        const index = this.active.findIndex(x => x == id);
        if(index !== -1)
            this.active.splice(index, 1);
        else this.active.push(id);

        this.active = [...this.active];
    }

    show() {
        this.updateEligible();
        this.updateStep();
        this.overlay.show();
    }

    hide() {
        this.overlay.hide();
        setTimeout(() => {
            this.step = 0;
        }, 300);
    }

    addProducts() {
        this.step = 1;
        setTimeout(() => {
            this.step = 2;
        }, 200);
    }

    back() {
        this.step = 1;
        setTimeout(() => {
            this.step = 0;
        }, 200);
    }


    async addWarranty() {
        this.step = 3;
        this.overlay.close = false;

        let warranties = {} as easyprotectInsured;
        const items = this.root.querySelectorAll(".warranty .items ks-cart-easyprotect-warranty");
        items.forEach(item => {
            warranties[item.getAttribute("product-id")] = item.getAttribute("active");
        });

        let fetchcomplete = false;
        let timeoutcomplete = false;
        setTimeout(() => {
            timeoutcomplete = true;
            if(fetchcomplete) this.end();
        }, 500);

        easyprotectChange(warranties).then(()=>{
            fetchcomplete = true;
            if(timeoutcomplete)  this.end();
        });
    }

    end() {
        this.overlay.hide();
        setTimeout(() => {
            this.overlay.close = true;
            this.step = 0;
            this.active = [];
        }, 300);
    }
}
