import { Component, h, Prop, Element, Host, State } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-easyprotect',
  styleUrl: 'cart-easyprotect.css'
})
export class CartEasyprotect {
    @Element() root: HTMLElement;
    @Prop() image: string;
    @Prop() width: number;
    @Prop() height: number;

    @State() insured: [string, string][];
    @State() enabled: boolean;

    componentWillLoad() {
        const update = () => {
            this.insured = Object.entries(cart.get("insured"));
            this.enabled = Object.keys(cart.get("easyprotect")).length > 0;
        }

        cart.onChange("insured", update);
        cart.onChange("easyprotect", update);
        update();
    }

    render() {
        return <Host class={!this.enabled ? "hidden" : ""}>
            <div class="top">
                <div class="content">
                    <slot/>
                </div>
                <ks-img2 src={this.image} width={this.width} height={this.height}/>
            </div>
            <div class="insured">
                { this.insured.map(([id, months]) => 
                    <ks-cart-easyprotect-warranty insured product-id={id} active={months}/>
                ) }
            </div>
            <slot name="bottom"/>
        </Host>;
    }
}