ks-cart-recycle {
    display: block;
    padding: 30px;
}

ks-cart-recycle h2 {
    font-size: 22px;
    text-transform: uppercase;
    font-family: var(--font-emphasis);
    font-weight: 700;
    margin-top: 20px;
}

ks-cart-recycle.hidden {
    display: none;
}

@media (max-width: 960px) {
    ks-cart-recycle {
        padding: 15px;
    }
}