import { Component, h, Element } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-deal-container'
})
export class CartDealContainer {
    @Element() root: HTMLElement;

    render() {
        return[
            cart.get("dealGroups").map((group) => 
                <ks-cart-deal-group
                    key={group.name}
                    name={group.name}
                    deals={group.deals}
                ></ks-cart-deal-group>
            ),
            cart.get("deals").map((product) => 
                <ks-cart-deal key={product.id}
                    ikey={product.id}
                    name={product.name}
                    link={product.link}
                    img={product.img}
                    price={product.price}
                ></ks-cart-deal>
            )
        ];
    }
}